/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable no-unused-vars */

import React from 'react';
import navLogo from '../images/logo.png'
import like from '../images/navImages/like.png'
import user from '../images/navImages/user.png'
import korzina from '../images/navImages/korzina.png'
import SearchIcon from '@material-ui/icons/Search'
const Nav = () => {
    return (
        <>
           <nav className="navigation">
               <a href="#" className="nav-logo">
                <img src={ navLogo } />
               </a>
            <div className="searchSec">
            <form>
                <input type="text"
                 className="searchInput"
                placeholder="Mahsulotlarni , Brendlarni qidirish"
                />
                <button className="searchBtn" type="button">
                <SearchIcon />
                </button>
            </form>
            </div>
            <div className="icons">
                <a href="#" className="bolimlar">
                <img src={ like } alt="Like"/>
                </a>
                <a href="#" className="bolimlar">
                <img src={ user } alt="User" />
                </a>
                <a href="#" className="bolimlar">
                <img src={ korzina } alt="Korzina" />
                </a>
            </div>

           </nav>
        </>
    );
};

export default Nav;