/* eslint-disable jsx-a11y/alt-text */
import React from 'react';
import ShoppingBasketIcon from '@material-ui/icons/ShoppingBasket';
import iPhones from '../images/iphonesbg.png'
const Home = () => {
    return (
        <div className="home">
            <div className="main">
            <div className="side1">
                <h3>
                iPhone 12 Proni yutib oling! 
                </h3>
                <p>
                    Juda kuchli kameralar orqali siz
                     betakror suratlar olishingiz mumkin!
                </p>
                <div className="buttons">
                    <a href="hbtn">
                        Xarid qilish!
                        <ShoppingBasketIcon />
                    </a>
                    <a href="hbtn2">
                       Atiga 599$  evaziga!
                    </a>
                </div>
            </div>
            <div className="side2">
                <img src={ iPhones } alt="iphones" />
            </div>
            </div>
        </div>
    );
};


export default Home;